.static-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background-color: #000; /* Pure black background */
}

/* Remove the gradient fallback since we're using pure black */
.static-background::before {
  display: none;
}

.stars {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(2px 2px at 20px 30px, #eee, transparent),
    radial-gradient(2px 2px at 40px 70px, rgba(255,255,255,0.8), transparent),
    radial-gradient(1px 1px at 90px 40px, #fff, transparent),
    radial-gradient(1px 1px at 130px 80px, rgba(255,255,255,0.6), transparent),
    radial-gradient(2px 2px at 160px 30px, #ddd, transparent);
  background-repeat: repeat;
  background-size: 200px 100px;
  animation: moveStars 20s linear infinite;
}

.twinkling {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(1px 1px at 50px 50px, #fff, transparent),
    radial-gradient(1px 1px at 100px 100px, rgba(255,255,255,0.8), transparent),
    radial-gradient(1px 1px at 150px 150px, #fff, transparent),
    radial-gradient(1px 1px at 200px 200px, rgba(255,255,255,0.6), transparent);
  background-repeat: repeat;
  background-size: 300px 300px;
  animation: moveStarsReverse 15s linear infinite;
}

.stars-distant {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(1px 1px at 25px 25px, rgba(255,255,255,0.4), transparent),
    radial-gradient(1px 1px at 75px 75px, rgba(255,255,255,0.3), transparent),
    radial-gradient(1px 1px at 125px 125px, rgba(255,255,255,0.5), transparent),
    radial-gradient(1px 1px at 175px 175px, rgba(255,255,255,0.2), transparent);
  background-repeat: repeat;
  background-size: 400px 400px;
  animation: moveStarsSlow 25s linear infinite;
}




@keyframes moveStars {
  0% {
    transform: translateX(0) translateY(0);
  }
  100% {
    transform: translateX(-200px) translateY(-100px);
  }
}

@keyframes moveStarsReverse {
  0% {
    transform: translateX(0) translateY(0);
  }
  100% {
    transform: translateX(300px) translateY(50px);
  }
}

@keyframes moveStarsSlow {
  0% {
    transform: translateX(0) translateY(0);
  }
  100% {
    transform: translateX(-400px) translateY(-200px);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
} 